@use "base/mixins" as m;
@use "base/variables" as v;
@use "sass:color";

.dashboard {
  background-color: v.$grisClaro;
  @include m.tablet {
    display: flex;
    height: 100vh;
  }
  .principal {
    flex: 1;
    overflow: scroll;
  }
  .contenido {
    padding: 2rem;
    
    .nombre-pagina {
      font-size: 2.8rem;
      text-align: center;
      text-transform: capitalize;
    }
  }
  .formulario {
    .campo input:not([type="submit"]) {
      background-color: v.$blanco;
    }
    input[type="submit"] {
      @include m.boton(v.$indigo, v.$blanco);
      &{padding: 1rem;
      text-transform: uppercase;
      font-weight: bold;
      font-size: 1.4rem;
      }
    }
  }
  .enlace{
    color: v.$indigo;
    &:hover{
      color: color.adjust($color: v.$indigo, $lightness: -20%);
      font-weight: bold;
    }
  }

}
