@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:color";

.filtros {
  margin-top: 1rem;
  background-color: color.adjust($color: v.$blanco, $lightness: 40%);
  padding: 2rem;
  border-radius: 0.5rem;
  .filtros-inputs {
    @include m.tablet {
      display: flex;
      align-items: center;
      gap: 3rem;
    }
  }
  h2 {
    color: v.$indigo;
    font-size: 2.4rem;
    margin-bottom: 0.5rem;
    @include m.tablet {
      margin-bottom: 0rem;
    }
  }
  input[type="radio"] {
    accent-color: v.$indigo;
    width: 1.6rem;
  }
  .campo {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    margin-bottom: 0.5rem;
    &:last-child {
      margin-bottom: 0rem;
    }
    @include m.tablet {
      margin-bottom: 0rem;
    }
  }
}
