@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal{
    position: absolute;
    background-color: rgb(0 0 0 / 0.8);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    translate: 50 50;
    align-items: center;
    .formulario{
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        opacity: 0;
        border-radius: 1rem;

        transition-property: all;
        transition-duration: 1000ms;



        &.animar{
        opacity: 1;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        }
        &.cerrar{
            transform: translateY(-200%);
            opacity: 0;
        }
    }

    input[type="text"]{
        border: 1px solid v.$gris;
        border-radius: 0.5rem;
    }
    legend{
        display: block;
        padding: 2rem;
        text-align: center;
        font-weight: bold;
        margin-bottom: 2rem;
    }

    .opciones{
        display: flex;
        justify-content: space-between;
    }
    .submit-nueva-tarea{
        @include m.boton(v.$indigo,v.$blanco);
    }
    .cerrar-modal{
        @include m.boton(v.$naranja,v.$blanco);
    }
    .nueva-tarea{

    }


}